@font-face { font-family: oskari; src: url('./fonts/Oskari.otf'); }
@font-face { font-family: euclidLight; src: url('./fonts/EuclidCircularA-Light.ttf'); }
@font-face { font-family: neutral; src: url('./fonts/NeutralFace.otf'); }

body {
  overflow: hidden;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  background: #000000;
}

.content{
  display: none;
  justify-content: center;
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: 1s;
}

canvas {
  position: fixed;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}



nav{
  width: 100vw;
  height: 5vh;
}

nav img{
  margin-top: 5vh;
  margin-left: 5vw;
  height: 20px;
}





#loader{
  position: fixed;
  z-index:6;
  left: 0;
  top: 0;
  text-align: center;
  width: 100%;
  display: flex;
  justify-content: center;
}

label{
  color: #f3f3f3;
}

#load{
  z-index: 3;
  position: fixed;
  bottom: 20vh;
  width: 30vw;
  height: 3px;
  background-color: rgb(48, 48, 48);
  display: flex;
  justify-content: flex-start;
  
}

#progress{
  position: relative;
  z-index:4 ;
  width: 100%;
  height: 100%;
  background-color: #fff;
  animation: loading 1.5s ease-out infinite alternate-reverse;
}

@keyframes loading {
  from{
    transform: scaleX(0);
  }

  to{
    transform: scaleX(100%);
  }
}

#intro{
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  align-items: center;
  width: 80vw;
  height: 80vh;
  
}

#intro h1{
  font-family: neutral;
  position: relative;

  font-size: 8vw;
  color: rgb(255, 255, 255);
  
}
#intro h2{
  font-family: euclidLight;
  position: relative;

  font-size: 1em;
  color: rgb(58, 58, 58);
  
}

#intro button{
  outline: none;
  border: 3px solid rgb(77, 77, 77);
  background-color: transparent;
  width: 30vw;
  height: 10vh;
  color: rgb(39, 39, 39);
  border-radius: 10px;
  font-size: 3vw;
  font-family: neutral;
  margin-top: 5vh;
  margin-bottom: 5vh;
}

#intro button:hover{
  transition: 0.5s;
  border: 5px solid rgb(255, 255, 255) ;
  color: #fff;
}



form{
  position: absolute;
  left: 10vw;
  top: 40vh;
  display: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.form-step{
  flex-direction: column;
  display: none;
  animation: animate .5s;
}


.form-step-active{
  display: inline;
  
}

@keyframes animate{
  from{
    transform: scale(1, 0);
    opacity: o;
  }
  t0{
    transform: scale(1, 1);
    opacity: 1;
  }
}



.form_field{
  padding-left: 30px;
  outline: none;
  width: 40vw;
  height: 10vh;
  background-color: rgb(22, 22, 22);
  border: none;
  border-radius: 30px;
  font-size: 30px;
  color: rgb(223, 223, 223);
  font-family: euclidLight;
}


#motivation{
  margin-top: -200px;
  padding-left: 30px;
  outline: none;
  width: 40vw;
  height: 30vh;
  background-color: rgb(22, 22, 22);
  border: none;
  border-radius: 30px;
  font-size: 30px;
  color: rgb(223, 223, 223);
  font-family: euclidLight;
  padding-top: 3%;
}

form #button_space{
  width: 23vw;
  display: flex;
  justify-content: space-between;
 
}

 .direction{
  display: flex;
  justify-content: center;
  align-items: center;
  outline: none;
  border: none;
  margin-top: 10vh;
  border-radius: 30px;
  width: 10vw;
  height: 10vh;
  background-color: rgb(250, 230, 50);
  font-family: euclidLight;
  font-size: 24px;

}

.progress-bar{
  width: 80vw;

  display: flex;
  justify-content: space-between;
  margin-bottom: 100% !important;
  margin-top: 5%;
  transition: 0.3s;
  /* margin-left: 25%; */
}

.progress-bar::before{
  content:"";
  position: absolute;
 

  height: 2px;
  width: 100%;
  background-color: rgb(44, 44, 44);
 
  
}
.progress-bar::before{
  z-index: -20;
}

.progress-step{
  margin-top: -1%;
  width: 2.6rem;
  height: 2.6rem;
  border-radius: 50%;
  background: rgb(44, 44, 44);
  
 
}

.progress-step-active{
  display: flex;
  background-color:rgb(250, 230, 50);
  color: #f3f3f3;
  
}


input{
  /* width: 50%; */
 
  display: flex;
  /* text-align: center; */

}

#end{
    color: white !important ;
    font-size: 4.3vw;
    display: flex;
    font-family: euclidLight;
    position: relative;
  
    color: rgb(58, 58, 58);
    
}

.cee{
  display: flex;
  justify-content: center;
}

a{
  text-decoration: none;
}
h4 {
  font-family: none;
  position: relative;

  font-size: 2vw;
  color: rgb(141, 125, 125);
}
